<template>
    <div style="width: 60px; border: 1px solid #d7d7d7; margin-bottom: 8px; padding: 4px 8px; cursor: pointer; "
        @click="showInput = true">add json</div>
    <div v-if="showInput" class="dialogContainer">
        <div style="font-size:22px;margin-bottom: 8px;font-weight: bolder;">請輸入Json</div>
        <div style="display: flex; align-items: center;margin-bottom: 8px;">
            <div style="width: 55px; border: 1px solid #d7d7d7;  padding: 4px 8px; cursor: pointer; margin-right: 8px;"
                @click="$refs.file.click()">上傳檔案</div>
            <span style="color: #919191">檔案內容: 須為json格式之.txt檔</span>
        </div>
        <input type="file" ref="file" style="display: none" @change="previewFiles($event)" />
        <textarea v-model="jsonText"></textarea>
        <span v-show="errorAlert" style="color: #f94f4f; position: absolute; left: 20px; bottom: 38px;">※{{ errorMessage
        }}</span>
        <div style="text-align: right; display: flex; justify-content: end;">
            <div class="button" @click="closeDialog">cancel</div>
            <div class="button" @click="doImport">import</div>
        </div>
    </div>
    <div v-if="showInput" class="mask" @click="closeDialog"></div>

</template>
<script>
export default {
    data() {
        return {
            showInput: false,
            errorAlert: false,
            jsonText: '',
            errorMessage: ''
        }
    },
    methods: {
        closeDialog() {
            this.showInput = false,
                this.jsonText = ''
            this.errorAlert = false
        },
        doImport() {
            try {
                const newJson = JSON.parse(this.jsonText)
                console.log(newJson)
                this.$emit('importJson', newJson)
                this.closeDialog()
            } catch (error) {
                console.log(error)
                this.errorMessage = '輸入內容非Json格式,請重新調整'
                this.errorAlert = true
            }
        },
        previewFiles(e) {
            const vm = this
            if (e.target.files[0].name.includes(".txt")) {
                this.errorAlert = false
                let fileData = e.target.files[0]
                let reader = new FileReader();
                reader.readAsText(fileData);
                reader.onloadend = async function (e) {
                    vm.jsonText = e.target.result
                }
            } else {
                vm.errorMessage = '上傳格式須為.txt檔,請重新上傳'
                this.errorAlert = true
            }


        }
    }
}
</script>
<style scoped>
textarea {
    outline: none;
    resize: none;
    width: 480px;
    height: 400px;
    border: 1px solid #d7d7d7;
    padding: 8px;
    line-height: 25px;
}

.dialogContainer {
    border: 1px solid #919191;
    border-radius: 4px;
    position: absolute;
    z-index: 3;
    top: 60px;
    left: 50%;
    transform: translate(-50%, 0%);
    padding: 20px;
    background-color: #ffffff;
}

.mask {
    z-index: 1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(131, 131, 131, 0.5);
    transition: opacity 200ms;
    width: 100%;
    height: 100%;
}

.button {
    padding: 8px;
    margin: 4px 0px 0px 12px;
    border: 1px solid;
    cursor: pointer;
}

.button:hover {
    transition: .3s;
    box-shadow: 0 0 5px #c6d3c6;
}

.button:first-child {
    border-color: #919191;
    background-color: #f1f0f0;
}

.button:last-child {
    border-color: #adc1a8;
    background-color: #d6f1cc;
}
</style>