<template>
  <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.2.0/css/all.min.css" />
  <div style="font-size:22px;margin-bottom: 8px;font-weight: bolder;">Json Creator</div>
  <importJsonDialog @importJson="importJson"></importJsonDialog>
  <div style="display: flex;">
    <div style="width: 65%; height: 800px; border: 1px solid #d7d7d7; overflow: auto; padding-left: 16px;">
      <JsonList :list="objList" @changeType="changeType" @addObj="addObj" @removeObj="removeObj"
        style="margin-right: 10px;"></JsonList>
    </div>
    <div style="height: 80vh; border-right: 1px solid #d7d7d7; margin: 0px 16px;"></div>
    <div style="width: 35%; height: 800px; border: 1px solid #d7d7d7; overflow: auto; padding-left: 22px;">
      <JsonDemo :list="objList" :first="true" :parentLength="objList.length"></JsonDemo>
    </div>
  </div>

</template>

<script>
import JsonList from './components/JsonList.vue'
import JsonDemo from './components/JsonDemo.vue'
import importJsonDialog from './components/importJsonDialog.vue'

export default {
  name: 'App',
  components: {
    JsonList,
    JsonDemo,
    importJsonDialog
  },
  data() {
    return {
      objList: [ {
        key: '',
        value: '',
        type: 'string',
        hasChildNode: false,
        show: true
      }],
    }
  },
  methods: {
    addObj(list) {
      let obj = {
        key: '',
        value: '',
        type: 'string',
        hasChildNode: false,
        show: true
      }
      list.push(obj)
    },
    changeType(val) {
      let obj = {
        key: '',
        value: '',
        type: 'string',
        hasChildNode: false,
        show: true
      }

      switch (val.type) {
        case 'string':
          val.hasChildNode = false
          val.value = ''
          delete val['objList']
          break;
        case 'object':
          val.hasChildNode = true
          val.objList = []
          val.objList.push(obj)
          break;
        case 'int':
          val.hasChildNode = false
          val.value = 0
          delete val['objList']
          break;
        case 'array':
          val.hasChildNode = true
          val.objList = []
          val.objList.push(obj)
          break;

      }
    },
    removeObj(val) {
      val.list.splice(val.index, 1)
    },
    importJson(json) {
      
      let result = this.assignList(json)
      console.log(result)
      this.objList = JSON.parse(JSON.stringify(result))

    },
    assignList(newJson) {
      const vm = this
      let objList = []
      let obj = {}
      Object.keys(newJson).forEach(item => {
        obj = {
          key: item,
          value: newJson[item],
          type: (Array.isArray(newJson[item])) ? 'array' : typeof(newJson[item]),
          hasChildNode: (typeof(newJson[item]) == 'object') ? true : false,
          objList: (typeof(newJson[item]) == 'object') ? vm.assignList(newJson[item]) : '',
          show: true
        }
        
        objList.push(obj)
      })
      return objList
    }
  },
}
</script>

