<template>
  <div>
    <div style="display: flex; margin-left: 8px;">
      <div>
        <div v-for="(item, index) in list" :key="item.id" style="margin-top: 10px;">
          <div v-if="index == 0">
            <i v-if="item.show" class="fa-solid fa-caret-down expand-icon" @click="collapseList()"></i>
            <i v-else class="fa-solid fa-caret-up expand-icon" @click="expandList()"></i>
            <template v-if="parentType!=='array'">{</template>
            <template v-else>[</template>
          </div>
          <div v-show="item.show" style="display: flex; border: 1px dashed #d7d7d7; padding: 8px; margin-left: 8px;">
            <i class="fa fa-xmark deleteBtn" @click.prevent="$emit('removeObj', {index: index, list: list})"></i>
            <div v-if="parentType!=='array'" style="display: flex;" >
              <input type="text" v-model="item.key">
              <div style="margin: 6px 8px 0px 8px; font-size: 16px; font-weight: bold;">:</div>
            </div>
            <div>
              <div v-if="!item.hasChildNode">
                <input v-if="item.type=='number'" type="number" v-model.number="item.value">
                <input v-else type="text" v-model="item.value">
              </div>
              <template v-else>
                <json-list
                  :list="item.objList"
                  style="margin-top: 6px;"
                  :parentType="item.type"
                  @changeType="changeType"
                  @addObj="addObj"
                  @removeObj="removeObj"></json-list>
              </template>
              <div style="margin-top: 8px;">
                <select v-model="item.type" @change="$emit('changeType', item)">
                  <option value="object">Object</option>
                  <option value="string">String</option>
                  <option value="array">Array</option>
                  <option value="number">Number</option>
                </select>
              </div>
            </div>
          </div>
          
          <div v-if="index == list.length-1">
            <div v-show="!item.show" style="margin-left:10px;margin-top: -8px">...</div>
            <div v-if="item.show" class="addBtn" @click.prevent="$emit('addObj', list)">+</div>
            <template v-if="parentType!=='array'">}</template>
            <template v-else>]</template>
          </div>
        </div>
      </div>
    </div>
    
  </div>

</template>
<script>
export default {
  name: "JsonList",
  props: ["list", "parentType"],
  methods: {
    changeType(val) {
      this.$emit('changeType', val)
    },
    addObj(val) {
      console.log(val)
      this.$emit('addObj', val)
    },
    removeObj(val) {
      console.log(val)
      this.$emit('removeObj', val)
    },
    collapseList() {
      this.list.forEach(item => {
        item.show = false
      })
    },
    expandList() {
      this.list.forEach(item => {
        item.show = true
      })
    }
  }
}

</script>
<style>
  .addBtn {
    cursor: pointer;
    margin-left: 8px;
    margin-top: 8px;
    margin-bottom: 8px;
    border-radius: 4px;
    width: 18px;
    height: 18px;
    text-align: center;
    line-height: 18px;
    font-size: 20px;
    background-color: #4aa1bf;
    color: #ffffff;
  }
  .addBtn:hover{
    transition: .3s;
    box-shadow: 0 0 10px #4aa1bf;
  }

  input {
    outline: none;
    height: 28px;
    border-radius: 4px;
    font-size: 14px;
    padding-left: 8px;
    border: 1px solid #d7d7d7;
    color: #3c4a58;
  }

  input:focus {
    transition: .3s;
    box-shadow: 0 0 3px #4aa1bf;
  }

  .deleteBtn {
    cursor: pointer;
    margin-right: 8px;
    margin-left: 4px;
    margin-top: 8px;
  }

  .deleteBtn:hover {
    color: #e35e5e;
  }

  select {
    border-radius: 4px;
    height: 22px;
    width: 80px;
    background-color: #d4e1e094;
    border: 0px;
    text-indent: 5px;
    outline: none;
    color:#5e758d;
  }
</style>