<template>
  <div>
    <div style="display: flex;">
      <div>
        <div v-for="(item, index) in list" :key="item.id" style="margin-top: 10px;">
          <div v-if="index == 0">
            <i v-if="item.show" class="fa-solid fa-caret-down expand-icon" @click="collapseList()"></i>
            <i v-else class="fa-solid fa-caret-up expand-icon" @click="expandList()"></i>
            <template v-if="parentType!=='array'">{</template>
            <template v-else>[</template>
          </div>

          <div v-show="item.show" style="display: flex; margin-left: 15px;">
            <template v-if="item.key && parentType!=='array'">"{{ item.key }}":</template>
            <template  v-if="item.value || (item.hasChildNode && item.objList.length) > 0">
              <template v-if="item.hasChildNode">
                <json-demo
                  :list="item.objList"
                  :first="false"
                  style="margin-left: 15px;"
                  :parentLength="list.length"
                  :nowIndex="index"
                  :parentType="item.type"
                  ></json-demo>
              </template>
              <span  v-else style="margin-left: 15px;" v-html="getValue(item)"></span>
              <template v-if="index!==list.length-1 && !item.hasChildNode">,</template>
            </template>
          </div>
          <div v-if="index == list.length-1">
            <div v-show="!item.show" style="margin-left:10px;margin-top: -8px">...</div>
            <template v-if="parentType!=='array'">}</template>
            <template v-else>]</template>
            <template v-if="!first && nowIndex!==parentLength-1">,</template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "JsonDemo",
  props: ["list", "first", "parentLength", "nowIndex", "parentType"],
  methods: {
    getValue(item) {
      switch(item.type) {
        case 'string':
          return `"${item.value}"`
        case 'number':
          return `<span style="color: #9a7db5">${item.value}</span>`
      }
    },
    collapseList() {
      this.list.forEach(item => {
        item.show = false
      })
    },
    expandList() {
      this.list.forEach(item => {
        item.show = true
      })
    }
  }
}

</script>